<template>
	<div class="resource-navigation-container h-full">
		<div class="resource-header flex flex-col justify-center items-center">
			<div class="search-navs flex items-center">
				<div
					class="navs-item"
					:class="{ 'navs-item--active': item.categoryName === activeNav }"
					v-for="item in navList"
					:key="item.id"
					@click="handleNavChange(item)"
				>
					{{ item.categoryName }}
				</div>
			</div>
			<div class="resource-search">
				<el-autocomplete
					size="large"
					v-model="search"
					clearable
          :trigger-on-focus="false"
					:placeholder="activePlaceholder"
          :fetch-suggestions="querySearch"
          :select-when-unmatched="true"
					@clear="handleClear"
          @select="handleSearch"
				>
					<template #append>
						<el-button @click="handleSearch">
							<SvgIcon name="handle-query-search" :size="20" class="handle-query-search" />
						</el-button>
					</template>
				</el-autocomplete>
			</div>
			<div class="mt-8 mb-5 flex justify-center">
				<BasicRound :list="roundList" :defaultValue="activeRound" @change="handleRoundChange" />
			</div>
		</div>

		<div class="resource-content flex">
			<div class="left-menu">
				<SingleMenu
					:elIcon="true"
					:default-key="menuDefaultKey"
					:menus="menus"
					class="h-full"
					@handleChange="handleMenuChange"
				/>
			</div>
			<div class="right-content flex-1" v-loading="loading">
				<div class="resource-tabs flex items-center" v-if="cateList.length > 0">
					<BaiscTabs :list="cateList" @handleTabChange="handleTabChange" />
				</div>
				<div :style="`height:calc(100% - ${cateList.length > 0 ? '60px' : '0px'});overflow-y:auto`">
					<BasicRow :list="list" v-if="list.length > 0" />
					<div v-else class="h-full flex justify-center items-center">
						<el-empty description="暂无数据" />
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script setup lang="ts">
	import { ref, reactive, computed, unref } from 'vue'
  import { MenuProps } from "@/components/SingleMenu/useSingleMenu"
	import SingleMenu from '@/components/SingleMenu/SingleMenu.vue'
	import BasicRow from '@/components/BasicCard/BaiscRow.vue'
	import { CardItem } from '@/components/BasicCard/types'
	import BaiscTabs from '@/components/BasicTabs/BasicTabs.vue'
	import BasicRound from '@/components/BasicRound/BasicRound.vue'
	import {
		fetchNavCategoryList,
		fetchNavCategorySearch,
		fetchNavList,
		fetchNavSearch
	} from '@/api/fetchs/nav'
	import { TabsItem } from '@/components/BasicTabs/type'
	import { useOnEmitFn } from '@/utils/useEmitter'
  import {sugrec} from "@/utils/file";
	const search = ref<string>('')
	const isSearch = ref<boolean>(false)
	const menus = ref<MenuProps[]>([])
	const menuDefaultKey = ref<string | number>('')
	const loading = ref<boolean>(true)
	const navList = ref<Recordable[]>([])
	const activeNav = ref<string>('')
	const activeRound = ref<string>('')
	const activeRoundUrl = ref<string>('')
	const roundList = computed<TabsItem[]>(() => {
		if (navList.value.length === 0) {
			return []
		}
		const rList = navList.value.find(v => v.categoryName === activeNav.value)?.childs
		return rList.map(v => ({
			name: v.name,
			id: v.name,
      searchUrl: v.searchUrl,
			placeholder: v.placeholder
		}))
	})
	const activePlaceholder = computed(() => {
		return unref(roundList).find(v => v.name === activeRound.value)?.placeholder
	})

	const cateList = ref<TabsItem[]>([])
	const list = ref<CardItem[]>([])
	const listQuery = reactive({
		id: 'root',
		tabId: ''
	})

	function handleMenuChange(menu) {
		isSearch.value = false
		search.value = ''
		list.value = []
		listQuery.id = menu.value
		const activeIdItem = menus.value.find(v => v.value === listQuery.id)
		if (activeIdItem && activeIdItem?.childs) {
			cateList.value = activeIdItem?.childs?.map(v => ({
				id: v.value,
				name: v.text
			})) as TabsItem[]
			if (activeIdItem?.childs.length === 0) {
				listQuery.tabId = listQuery.id
				getList()
			}
		} else {
			cateList.value = []
		}
	}

	function handleTabChange(item) {
		listQuery.tabId = item.id
		getList()
	}

	function handleNavChange(item) {
		activeNav.value = item.categoryName
		const navItem: any = unref(navList).find(v => v.categoryName === activeNav.value)
		if (navItem && navItem.childs) {
			activeRound.value = navItem.childs[0].name
      activeRoundUrl.value = navItem.childs[0].searchUrl
		}
	}
	function handleRoundChange(val) {
		activeRound.value = val

    const activeSearchUrl = computed(() => {
      return unref(roundList).find(v => v.name === activeRound.value)?.searchUrl
    })

    activeRoundUrl.value = activeSearchUrl.value
	}

	function handleClear() {
		isSearch.value = false
		setDefaultMenuValue()
	}

	function handleChange(val) {
		if (val) {
			handleSearch()
		} else {
			isSearch.value = false
			setDefaultMenuValue()
		}
	}

	function setDefaultMenuValue() {
		menuDefaultKey.value = menus.value.length > 0 ? menus.value[0].value : 'root'
	}

  function querySearch(queryString, cb) {
    sugrec(queryString).then((res) => {
      if (res && res.length > 0) {
        cb(res);
      }
    });
  }

	useOnEmitFn('handleUpdateList', getList)
	/* ================================== fetchs ================================== */
	async function getCategorylist() {
		const { data } = await fetchNavCategorySearch()
		navList.value = data
		if (data && data.length > 0) {
      let defaultActive = data.find(v => v.default)
      if(!defaultActive) {
        defaultActive = data[0]
      }

      activeNav.value = defaultActive.categoryName
			activeRound.value = defaultActive.childs && defaultActive.childs.length > 0 ? defaultActive.childs[0].name : ''
			activeRoundUrl.value = defaultActive.childs && defaultActive.childs.length > 0 ? defaultActive.childs[0].searchUrl : ''
		}
	}
	async function getNavList() {
		const { data } = await fetchNavCategoryList()
		menus.value = data.map(v => {
			return {
				icon: v.icon,
				text: v.name,
				value: v.id,
				childs: v.childs.map(i => ({
					value: i.id,
					text: i.name
				}))
			}
		})
		if (menus.value.length > 0) {
			setDefaultMenuValue()
			listQuery.id = menus.value[0].value as string
			cateList.value = menus.value[0].childs?.map(v => ({
				id: v.value,
				name: v.text
			})) as TabsItem[]
		}

		loading.value = false
	}
	getCategorylist()
	getNavList()
	async function getList() {
    if(isSearch.value) {
      const { data } = await fetchNavSearch({
        categoryName: activeRound.value,
        search: search.value
      })
      list.value = data
    } else {
      const { data } = await fetchNavList({
        pid: listQuery.tabId
      })
      list.value = data
    }
	}

	async function handleSearch() {
    console.log(search.value)
		if (!search.value) {
			return
		}
    if(activeRoundUrl.value) {
      window.open(activeRoundUrl.value + search.value)
    }else {
      const { data } = await fetchNavSearch({
        categoryName: activeRound.value,
        search: search.value
      })
      isSearch.value = true
      list.value = data

      const searchItem:TabsItem[] = [{id:'搜索结果',name:'搜索结果'}]
      cateList.value = searchItem

      menuDefaultKey.value = ''
    }

	}
</script>

<style lang="scss">
	$--resource-header-height: 230px;
	.resource-navigation-container {
		background-color: $--content-bg-color;
		.resource-header {
			height: $--resource-header-height;
			background-image: url("http://i.navigationline.cn/system/bg/resource-bg.png");
      -moz-background-size:100% 100%;
      background-size:100% 100%;
			color: $--white-color;
			padding-top: 20px;
			.search-title {
				font-size: 26px;
				margin-bottom: 20px;
			}
			.search-navs {
				margin-bottom: 15px;
				.navs-item {
					padding: 3px 7px;
					margin: 0 3px;
					position: relative;
					font-size: 14px;
					cursor: pointer;
					transition: 0.2s;
					&.navs-item--active {
						background-color: #ffd100;
						border-radius: 4px;
						&::before {
							display: block;
							content: '';
							position: absolute;
							left: 50%;
							bottom: -6px;
							transform: translateX(-50%);
							width: 0;
							height: 0;
							border-left: 10px solid transparent;
							border-right: 8px solid transparent;
							border-top: 10px solid #ffd100;
							border-radius: 50px;
						}
					}
				}
			}
			.resource-search {
				width: 650px;
				.handle-query-search {
					color: $--white-color;
				}

				.el-input-group__append {
					background-color: var(--el-color-primary);
					box-shadow: 0 1px 0 0 var(--el-color-primary) inset,
						0 -1px 0 0 var(--el-color-primary) inset, -1px 0 0 0 var(--el-color-primary) inset;
					.el-button {
						margin: 0 -10px;
						padding: 5px 15px;
					}
				}

        .el-autocomplete{
          width: 100%;
        }
			}
		}
		.resource-content {
			height: calc(100% - #{$--resource-header-height});
			.left-menu {
				width: 150px;
				height: 100%;
				padding-left: 20px;
				background-color: $--white-color;
			}
			.right-content {
				width: calc(100% - 150px);
				height: 100%;
				.resource-tabs {
					width: 100%;
					background-color: $--white-color;
					padding: 0 10px;
					height: 60px;
					.el-tabs {
						width: 100%;
					}
				}
			}
		}
	}
</style>
