<template>
	<el-tabs v-model="activeName" class="demo-tabs" @tab-click="handleClick">
		<el-tab-pane
			:label="item.name"
			:name="item.id"
			v-for="item in list"
			:key="item.id"
		></el-tab-pane>
	</el-tabs>
</template>

<script setup lang="ts">
	import { ref, watch } from 'vue'
	import { TabsItem } from './type'

	const props = defineProps<{
		list: TabsItem[]
	}>()

	const activeName = ref<string | undefined>(props.list[0].id)
	const emit = defineEmits(['handleTabChange'])

	emit('handleTabChange', props.list[0])
	watch(
		() => props.list,
		val => {
			if (val.length > 0) {
				activeName.value = val[0].id
				emit('handleTabChange', val[0])
			}
		},
		{ deep: true, immediate: true }
	)

	function handleClick(item) {
		const { index } = item
		const value = props.list[index]
		emit('handleTabChange', value)
	}
</script>
